import { StaggeredContainer, StaggeredItem } from "../../../animations/staggeredFadeIn";
import { ContentHeader, Description, Wrapper } from "../styled";
import { AboutWrapper } from "./styled";


const About = () => (

    <Wrapper id="about">
        <StaggeredContainer>
            <AboutWrapper>
                <StaggeredItem>
                    <ContentHeader>
                        Nasza dziaalno
                    </ContentHeader>
                </StaggeredItem>
                <StaggeredItem>
                    <Description>
                        Nasz wyspecjalizowany zesp od wielu lat zajmuje si pozyskiwaniem rodkw na rozwj dziaalnoci/zakadanie dziaalnoci. W oparciu o nasze dowiadczenie przeprowadzimy Ci przez cay proces uzyskania funduszy. Zajmujemy si znalezieniem odpowiedniego dofinansowania pod Twj biznes, weryfikujemy ju na wstpie szanse na uzyskanie rodkw, kompletujemy wsplnie niezbdn dokumentacje, a nastpnie tworzymy projekt gotowy do zoenia. Jestemy wsparciem przy Tworzeniu, rozliczeniu, uzupenieniu wniosku.
                    </Description>
                </StaggeredItem>
            </AboutWrapper>
        </StaggeredContainer>
    </Wrapper>
);

export default About;